.powerLine {
  position: fixed;
  bottom: 15px;
  height: 7px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  background: transparent;
  box-shadow: inset 0px 0px 5px black;
  overflow: hidden;
  /* outline: 2px solid black; */
}

.powerLine .line {
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  height: 100%;
}
